import axios from 'axios';

export function getServerLabel(url: string): string {
  if (!url) return 'Unknown Server';
  try {
    const hostname = new URL(url).hostname;
    return hostname.replace(/^www\./, '');
  } catch {
    return 'Unknown Server';
  }
}

export function buildEmbedUrl(
  providerValue: string,
  mediaType: "movie" | "tv",
  tmdbId: number,
  season?: number,
  episode?: number
): string {
  if (!providerValue) throw new Error('Provider URL required');

  // Handle template URLs (${id}, ${season}, ${episode})
  if (/\$\{.*\}/.test(providerValue)) {
    return providerValue
      .replace(/\$\{id\}/g, String(tmdbId))
      .replace(/\$\{type\}/g, mediaType)
      .replace(/\$\{season\}/g, String(season || 1))
      .replace(/\$\{episode\}/g, String(episode || 1));
  }

  // Handle base URLs - build path
  const base = providerValue.endsWith('/') ? providerValue.slice(0, -1) : providerValue;
  if (mediaType === 'movie') {
    return `${base}/movie/${tmdbId}`;
  } else {
    return `${base}/tv/${tmdbId}/${season || 1}/${episode || 1}`;
  }
}

export async function resolveTmdbId(input: string | number, type: "movie" | "tv"): Promise<number | null> {
  // Handle numeric input
  if (typeof input === 'number' && Number.isFinite(input)) return input;
  
  const s = String(input).trim();
  if (!s) return null;

  // Handle string numeric
  const asNum = Number(s);
  if (Number.isFinite(asNum)) return asNum;

  // Handle IMDb IDs
  if (/^tt\d+$/i.test(s)) {
    const API_KEY = process.env.NEXT_PUBLIC_TMDB_API_KEY?.trim();
    if (!API_KEY) {
      console.warn('TMDB API key required for IMDb ID resolution');
      return null;
    }
    
    try {
      const { data } = await axios.get(`https://api.themoviedb.org/3/find/${s}`, {
        params: { api_key: API_KEY, external_source: 'imdb_id' },
        timeout: 5000
      });
      
      const results = type === 'movie' ? data.movie_results : data.tv_results;
      return results?.[0]?.id ?? null;
    } catch (err) {
      console.warn('IMDb ID resolution failed:', err);
      return null;
    }
  }
  
  return null;
}
